<template lang="html">
  <div class="main-rule">
    <!-- <info-title context="查询条件"></info-title> -->
    <form-group :form-content="formContent" class="form-group">
      <el-col :span="6">
        <el-button type="success" size="small" @click="addForm" icon="el-icon-plus">新增</el-button>
        <el-button type="primary" size="small" @click="searchForm" icon="el-icon-search">查询</el-button>
        <el-button size="small" @click="clearForm" icon="el-icon-delete">清空</el-button>
      </el-col>
    </form-group>
    <div class="table-wrapper">
      <div class="table-group">
        <el-table ref="mainTable"
                  :data="tableData"
                  v-loading="loadingTable"
                  :max-height="tableHeight"
                  element-loading-text="拼命加载中"
                  border>
                  <el-table-column v-for="(col,index) in tableColumnLabel"
                                  :key="col.label"
                                  :prop="col.label"
                                  :label="col.value"
                                  :width="col.width"
                                  :formatter="col.formatter">
                  </el-table-column>
                  <el-table-column prop="operate"
                                   label="操作"
                                   width="230"
                                   :formatter="formatter">
                                   <template slot-scope="scope">
                                     <el-button v-for='btn in scope.row.operate'
                                               :key='btn'
                                               size="small"
                                               :type="operateBtn(btn,'type')"
                                               @click="handleOperateBtn(scope.$index, scope.row)">{{operateBtn(btn,'text')}}
                                     </el-button>
                                   </template>
                  </el-table-column>
        </el-table>
      </div>
      <el-pagination class="table-pagination"
                     background
                     layout="prev, pager, next"
                     :page-size="paginationSize"
                     :total="paginationLen"
                     @current-change="handlePagination">
      </el-pagination>
    </div>

    <m-dialog :form-data="mainRulesAddModalData"
              :form-visible="isShowDialog"
              @on-close="handleCloseDialog"
              @on-confirm="handleConfirmDialog">
    </m-dialog>

  </div>
</template>

<script>
import '@/util/mock'
import {
  fetchPost,
  fetchJson
} from '@/util/fetch'

import {
  mapState
} from 'vuex'
import FormGroup from '@/components/base/formGroup'
import InfoTitle from '@/components/base/moduleInfoTitle'
import MDialog from '@/components/base/dialogModal'

const PAGESIZE = 9
export default {
  components: {
    FormGroup,
    InfoTitle,
    MDialog
  },
  data() {
    return {
      tHeight:'',
      loadingTable: false,
      isShowDialog: false,
      tableColumnLabel: [{
        label: 'level',
        value: '优先级',
        width: 100
      }, {
        label: 'ruleName',
        value: '规则名称'
      }, {
        label: 'ruleType',
        value: '规则类型'
      }, {
        label: 'sTime',
        value: '起始时间'
      }, {
        label: 'eTime',
        value: '结束时间'
      }, {
        label: 'isNeedOrder',
        value: '是否需要订单'
      }, {
        label: 'isRun',
        value: '是否启用'
      }],
      tableData: [],

      paginationLen: null,
      paginationSize: null
    }

  },
  computed: {
    ...mapState(["ruleFormLabelAlign", "mainRulesAddModalData"]),

    formContent() {
      return this.ruleFormLabelAlign
    },
    tableHeight() {
      // this.$nextTick(()=>{
      //   let tableHeight = this.$refs.mainTable.$el.clientHeight
      //   let renderHeight = document.body.clientHeight - 230
      //   console.log(tableHeight,renderHeight)
      //   if (tableHeight <= renderHeight) {
      //     return 'auto'
      //   } else {
      //     return document.body.clientHeight - 230
      //   }
      // })
      return document.body.clientHeight - 230
    }
  },
  created() {
    const pageSize = PAGESIZE
    let mainRulesData = fetchJson('/mainRules-data', {
      pagination: true,
      Size: pageSize,
      Index: 1
    }).then(res => {
      // console.log(Math.floor(res.data.length / pageSize) + 1)
      this.tableData = res.data.tableData
      this.paginationLen = res.data.length
      this.paginationSize = pageSize
    });

    // console.log(mainRulesData.code);
  },
  mounted() {
    this.$nextTick(() => {
      // console.log(this.$refs.mainTable.$el.clientHeight)
    })

  },
  methods: {
    operateBtn(btn, type) {
      let btnType = ''
      let btnText = ''
      switch (btn) {
        case 0:
          btnType = ''
          btnText = '启用'
          break
        case 1:
          btnType = 'primary'
          btnText = '编辑'
          break
        case 2:
          btnType = 'info'
          btnText = '禁用'
          break
        case 3:
          btnType = 'success'
          btnText = '查看'
          break
        case 4:
          btnType = 'danger'
          btnText = '删除'
          break
      }
      if (type === 'text') {
        return btnText
      } else {
        return btnType
      }
    },
    handlePagination(index) {
      console.log(index)
      this.loadingTable = true
      let mainRulesData = fetchJson('/mainRules-data', {
        pagination: true,
        Size: PAGESIZE,
        Index: index
      }).then(res => {
        this.timer = setTimeout(() => {
          this.loadingTable = false
          this.tableData = res.data.tableData
          clearTimeout(this.timer)
        }, 1200)
      });
    },
    handleOperateBtn(index, row) {
      console.log(index, row)
    },
    searchForm() {
      console.log(this.$store.state.ruleFormLabelAlign)
    },
    clearForm() {
      Object.keys(this.ruleFormLabelAlign).forEach(key => {
        this.ruleFormLabelAlign[key].value = key === 'time' ? [] : ''
      })
      console.log(this.$store.state.ruleFormLabelAlign)
    },
    addForm() {
      this.isShowDialog = true
    },
    handleCloseDialog() {
      this.isShowDialog = false
    },
    handleConfirmDialog() {
      this.isShowDialog = false
    },
    filterBtn(value, row, column) {
      console.log(value, row, column)
    },
    formatter(row, column) {
      return ''
    },
  }
}
</script>
<style media="screen" lang="stylus">
  // .el-table--scrollable-y
  //   .el-table__body-wrapper

</style>
<style lang="stylus" scoped>
.form-group
  padding 10px 20px
  button
    margin-top 34px
.table-wrapper
  position relative
  padding 0 20px
  // max-height calc(100% - 260px)
  // overflow-y auto
  // .table-group
  //   max-height calc(100% - 260px)
  //   overflow auto
  .el-table
    width 99%
  .table-pagination
    position absolute
    left 10px
    margin-top 20px
</style>



